
<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('CATEGORY_NAME') }}</label>
          <vs-input class="w-full" v-model="reportCategory.categoryName"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyReportCategory('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyReportCategory('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyReportCategory('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import ReportCategoryService from '@/services/report/ReportCategoryService'

export default {
  name: 'report-category-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      reportCategory: {}
    }
  },
  methods: {
    async load () {
      let self = this
      ReportCategoryService.reportCategories({
        categoryId: this.$route.params.id
      }).then(function (response) {
        self.reportCategory = response.data.reportCategoryList[0] || {}
      })
    },
    modifyReportCategory (nextAction) {
      let self = this
      ReportCategoryService.modifyReportCategory({
        categoryId: this.$route.params.id,
        categoryName: this.reportCategory.categoryName
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewReportCategory', params: { id: response.data.categoryId } })
          } else {
            self.$router.push({ name: 'BrowseReportCategories' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
